  text-decoration: none;
  position: relative;
  transition: 0.3s ease;
}
header .link:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #403F3D;
  position: absolute;
  left: 0;
  bottom: -4px;
  transform: scaleX(0);
  transition: transform 0.5s ease;
}
header #link-active {
  cursor: default;
}
header #link-active:before {
  transform: scaleX(1);
