ody;\n      border: 0;\n      border-radius: 6.25rem;\n\n      cursor: pointer;\n\n      span {\n        transform: translateX(29%);\n        transition: all .5s ease;\n      }\n\n      svg {\n        opacity: 0;\n        margin-left: 4px;\n        animation: btn-opacity-0 .3s ease;\n      }\n    }\n  }\n}\n\n@media (hover:hover) {\n  .enjoy__button:hover {\n    button {\n      span {\n        transform: translateX(0%);\n        transition: all .5s ease;\n      }\n\n      svg {\n        animation: btn-opacity-1 .3s ease forwards;\n      }\n    }\n  }\n}\n\n@keyframes btn-opacity-1 {\n  0% {\n    opacity: 0%;\n  }\n  50% {\n    opacity: 50%;\n  }\n  100% {\n    opacity: 100%;\n  }\n}\n\n@keyframes btn-opacity-0 {\n  0% {\n    opacity: 100%;\n  }\n  50% {\n    opacity: 50%;\n  }\n  100% {\n    opacity: 0%;\n  }\n}","header {\n  display: flex;\n  justify-content: space-between;\n\n  margin: 20px 0;\n  height: 60px;\n\n  nav {\n    flex-basis: 39.5%;\n    margin: 15px;\n  }\n\n  ul {\n    display: flex;\n    justify-content: space-evenly;\n\n    list-style: none;\n  }\n\n  .link {\n    @include link-button;\n    color: $text-dark;\n    text-decoration: none;\n\n    position: relative;\n    transition: 0.3s ease;\n  }\n\n  .link:before {\n    content: '';\n    width: 100%;\n    height: 2px;\n    background-color: $text-dark;\n    position: absolute;\n    left: 0;\n    bottom: -4px;\n    transform: scaleX(0);\n    transition: transform .5s ease;\n  }\n\n  #link-active {\n    cursor: default;\n  }\n\n  #link-active:before{\n    transform: scaleX(1);\n  }\n\n  .header__menu-link {\n    margin: 15px 0;\n\n    img {\n      margin-left: 0.28rem;\n    }\n  }\n}\n\n\n@media (hover:hover) {\n\n  header {\n    .link:hover:before {\n      transform: scaleX(1);\n      transition: transform .5s ease;\n    }\n  }\n}",".enjoy {\n  border-radius: 2.5rem;\n  background-image: url(./../assets/img/img-hero.png);\n  background-position: center;\n  background-size: cover;\n\n  width: 100%;\n